.banner-principal{width:100%;height:100vh;background-image:url(/static/media/bannerPrincipal.ce904e24.jpg);background-repeat:no-repeat;background-size:contain;background-size:cover;position:relative;z-index:1;display:flex;flex-direction:column;align-items:center;justify-content:center}.banner-principal a{font-weight:bold;text-decoration:none;color:#fff;margin:7px 0}.banner-principal a:hover{text-decoration:underline}.banner-principal .slayer{width:100%;height:100vh;background-color:#000;position:absolute;z-index:2;opacity:.6}.banner-principal .containerFormulario{width:400px;height:410px;background:#42a5f5;position:relative;z-index:3;border-radius:5px;border:6px solid rgba(120,120,120,.5);background-clip:padding-box}.banner-principal .containerFormulario form{display:flex;flex-direction:column;align-items:center;justify-content:center;padding-top:45px;padding-bottom:45px}.banner-principal .containerFormulario form h2{font-size:40px;font-weight:bold;color:#fff}.banner-principal .containerFormulario form h2 .cadeado{position:relative;top:5px}.banner-principal .containerFormulario form h3{font-size:18px;font-weight:normal;padding-bottom:18px}.banner-principal .containerFormulario form .input-wraper{width:100%;padding:3px 0;display:flex;align-items:center;justify-content:center}.banner-principal .containerFormulario form .input-wraper input[type=email],.banner-principal .containerFormulario form .input-wraper input[type=password],.banner-principal .containerFormulario form .input-wraper input[type=text]{width:65%;padding:10px 5px;border:none;outline:0;border:2px solid #ccc;border-radius:8px}.banner-principal .containerFormulario form .input-wraper button{outline:0;border:none;cursor:pointer;font-size:18px;width:50%;padding:8px;margin-top:8px;background-color:#0d47a1;color:#fff;transition:1s}.banner-principal .containerFormulario form .input-wraper button:hover{background:#10366e}.banner-principal .containerFormulario form .input-wraper span{padding:4px 7px;font-size:23px;padding-right:5px;background-color:#d4d4d4}
.dashboard{display:flex;flex-direction:column;justify-content:center;align-items:center}.dashboard span{margin:2em 0;font-weight:600;font-size:1.2em}.dashboard .new{float:right;margin-bottom:1.5em;padding:.5em;background-color:#83bf02;color:#fff;border:0;border-radius:5px;display:flex;justify-content:center;align-items:center;font-weight:600;font-size:1.1em;transition:ease-in .2s}.dashboard .new:hover{background-color:#5fd2a4;transform:scale(1.1)}.dashboard .new svg{margin-right:4px}.btn-more{margin:1.5em 0;padding:.5em 1em;height:35px;border:0;border-radius:5px;background-color:#181c2e;color:#fff;font-size:1.1em}.new2{float:right;margin-bottom:1.5em;padding:.5em;background-color:#83bf02;color:#fff;border:0;border-radius:5px;display:flex;justify-content:center;align-items:center;font-weight:600;font-size:1.1em;transition:ease-in .2s}.new2:hover{background-color:#5fd2a4;transform:scale(1.05)}.new2 svg{margin-right:4px}table{width:100%;table-layout:fixed;border:1px solid #ccc;border-collapse:collapse;margin:0;padding:0}table caption{font-size:1.5em;margin:.5em 0 .75em}table tr{background-color:#f8f8f8;border:1px solid #ddd;padding:.35em}table th{font-size:.85em;letter-spacing:.1em;text-transform:uppercase}table td .action{border-radius:4px;border:0;padding:5px;margin-right:2px;align-items:center;display:inline-block}table td .action svg{vertical-align:middle}table td .badge{padding:3px;border-radius:3px;color:#fff}table th,table td{padding:.62em;text-align:center}#copy{transition:.5s}#copy:hover{-webkit-filter:brightness(0.8);filter:brightness(0.8)}@media screen and (max-width: 600px){table{border:0}table caption{font-size:1.3em}table thead{border:none;clip:rect(0 0 0);height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;width:1px}table tr{border-bottom:3px solid #ddd;display:block;margin-bottom:.65em}table td{border-bottom:1px solid #ccc;display:block;font-size:.8em;text-align:right}table td:before{content:attr(data-label);float:left;font-weight:bold;text-transform:uppercase}table td:last-child{border-bottom:0}}
