.container{display:flex;align-items:center;margin-right:25px;background-color:#f8f8f8;border-radius:5px;position:relative;top:0;left:16px;padding-bottom:50px}.container .form-profile img{margin-bottom:1em;border-radius:50%;object-fit:cover}.container .form-profile label.titulo{width:100%;display:block;font-size:1.4em;margin-bottom:.5em}.container .form-profile button{width:150%;max-width:600px;height:35px;border:0;border-radius:7px;font-size:1.3em;background-color:#181c2e;color:#fff;transition:.3s}.container .form-profile button:hover{-webkit-filter:brightness(1.5);filter:brightness(1.5)}.container .form-profile input,.container .form-profile textarea,.container .form-profile select{width:150%;max-width:600px;margin-bottom:1em;padding:.7em;border:0;outline:0;border-radius:5px}.container .form-profile input:disabled{cursor:not-allowed;-webkit-filter:brightness(1.5);filter:brightness(1.5)}.container .label-avatar{width:280px;height:280px;display:flex;flex-direction:column;justify-content:center;cursor:pointer}.container .label-avatar input{display:none}.container .label-avatar span{position:absolute;margin-left:110px;z-index:999;opacity:.7;transition:all .5s}.container .label-avatar span:hover{opacity:1;transform:scale(1.4)}.container .sair{padding:8px 20px;background-color:transparent;border:2px solid #121212;border-radius:5px;font-size:1.5em;display:flex;justify-content:center;align-items:center}
